.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    margin: 0 auto;

    width: 80%;
    height: 100%;
    border: 1px solid blue;
}

@media screen and (max-width: 1200px) {
    .container {
        width: 60%;
        justify-content: center;
        border: 1px solid blue;
    }

}
